.header {
  &__top {
    max-width: 1330px;
    padding: 10px 15px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #F0F0F0;
  }
  &__top-links {
    display: flex;
    align-items: center;
  }
  &__top-link {
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    color: $gray-text;
    padding: 3px 50px;
    border: 1px solid $lgray;
    border-radius: 5px;
    margin-right: 20px;
    transition: .2s ease-in-out;
    &:last-child  {
      margin-right: 0;
    }
    &:hover {
      background-color: #f5f5f5;
      transition: .2s ease-in-out;
    }
    @media (max-width: 767px) {
      padding: 3px 15px;
    }
    @media (max-width: 600px) {
      margin-right: 10px;
      padding: 0;
      border: 0;
      font-size: 11px;

      &:last-child  {
        margin-right: 0;
      }
    }
  }
  &__top-mail {
    font-size: 14px;
    line-height: 17px;
    color: $gray-text;
    &:hover {
      text-decoration-color: $gray-text;
    }
    @media (max-width: 600px) {
      font-size: 12px;
      line-height: 15px;
    }
  }
  &__middle {
    display: flex;
    align-items: center;
    max-width: 1330px;
    margin: 0 auto;
    padding: 20px 15px;
  }
  &__logo-descriptor-wrapper {
    display: flex;
    align-items: center;
    margin-right: 15px;
    @media (max-width: 1199px) {
      margin-right: auto;
    }
  }
  &__logo {
    margin-right: 20px;
    svg {
      width: 200px;
      height: 48px;
      fill: #24a544;
      @media (max-width: 600px) {
        width: 150px;
        height: 36px;
      }
    }
  }
  &__descriptor {
    font-size: 16px;
    line-height: 19px;
    color: $gray-text;
    max-width: 250px;
    @media (max-width: 991px) {
      display: none;
    }
  }
  &__slogan {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: $gray-text;
    padding: 6px 45px;
    border: 1px solid #FF8A00;
    box-shadow: 0 11px 34px rgba(255, 138, 0, 0.2);
    border-radius: 5px;
    margin: 0 auto;
    max-width: 200px;
    background-color: $white;
    @media (max-width: 1199px) {
      margin-bottom: 20px;
    }
  }
  &__contacts {
    display: flex;
    align-items: center;
    margin-left: 15px;
    @media (max-width: 991px) {
      margin-left: 0;
      margin-right: 20px;
    }
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__contacts-messenger {
    display: flex;
    align-items: center;
    margin-right: 25px;
    &:hover {
      .header__contacts-messenger-phone {
        color: #40B3E0;
        transition: .2s ease-in-out;
      }
      .header__contacts-messenger-text {
        color: #40B3E0;
        transition: .2s ease-in-out;
      }
      .icon-tg {
        box-shadow: 0 11px 34px rgba(64, 179, 224, 0.3);
        transition: .2s ease-in-out;
      }
    }
    @media (max-width: 767px) {
      margin-right: 0;
      margin-bottom: 25px;
    }
    svg {
      width: 46px;
      height: 46px;
      margin-right: 7px;
      border-radius: 50%;
      overflow: hidden;
      transition: .2s ease-in-out;
    }
  }
  &__contacts-messenger-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__contacts-messenger-phone {
    font-weight: 300;
    font-size: 19px;
    line-height: 22px;
    color: $gray-text;
    transition: .2s ease-in-out;
    @media (max-width: 767px) {
      color: $white;
    }
  }
  &__contacts-messenger-text {
    font-size: 12px;
    line-height: 14px;
    text-decoration: underline;
    color: $gray-text;
    transition: .2s ease-in-out;
    @media (max-width: 767px) {
      color: $white;
    }
  }
  &__contacts-phone {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5px 10px;
    background-color: $green;
    box-shadow: 0 11px 34px rgba(36, 165, 68, 0.2);
    border-radius: 5px;
    @media (max-width: 767px) {
      padding-left: 53px;
    }
  }
  &__contacts-phone-tel {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: $white;
    &:hover {
      text-decoration-color: $white;
    }
    @media (max-width: 767px) {
      font-weight: 300;
      font-size: 19px;
      line-height: 22px;
    }
  }
  &__contacts-callback {
    font-size: 12px;
    line-height: 14px;
    text-decoration: underline;
    color: $white;
    cursor: pointer;
    &:hover {
      text-decoration-color: transparent;
    }
  }
  &__mobile-menu-toggle {
    display: none;
    cursor: pointer;
    svg {
      width: 35px;
      height: 35px;
      fill: $green;
      @media (max-width: 600px) {
        width: 25px;
        height: 25px;
      }
    }
    @media(max-width: 991px) {
      display: block;
    }
  }
  &__bottom {
    background: #F0F0F0;
  }
}