.container {
  max-width: 1330px;
  padding: 0 15px;
  margin: 0 auto;
}

.clickable-bg {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
  z-index: 2;
  &--dark {
    background-color: $black;
    opacity: 0.7;
  }
}

.main-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1330px;
  padding: 22px 15px;
  margin: 0 auto;
  @media (max-width: 991px) {
    flex-direction: column;
    max-width: 100%;
    padding: 0;
    align-items: stretch;
    margin: 0 -20px 30px;
  }
  &__item {
    max-width: 125px;
    flex-basis: 125px;
    margin: 0 5px;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #054428;
    &:hover {
      color: #24A544;
    }
    @media (max-width: 991px) {
      max-width: calc(100% + 40px);
      width: calc(100% + 40px);
      flex-basis: auto;
      text-align: left;
      color: $white;
      border-bottom: 1px solid $white;
      padding: 15px;
      margin: 0;
      &:hover {
        text-decoration-color: transparent;
      }
    }
  }
}

.common-section {
  margin-bottom: 50px;
}

.h2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  color: $gray-text;
  margin-bottom: 30px;
  @media (max-width: 767px) {
    font-size: 24px;
    line-height: 28px;
  }
}

.green-txt {
  color: $green;
}

.mobile-only {
  display: none;
  @media (max-width: 991px) {
    display: block;
  }
}

.show-more-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.show-more {
  display: flex;
  align-items: center;
  padding: 10px 20px;
  background-color: $orange;
  border-radius: 5px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  &:hover {
    box-shadow: 0px 11px 34px rgba(255, 138, 0, 0.4);
  }
  @media (max-width: 767px) {
    font-size: 16px;
    line-height: 21px;
    padding: 7px;
  }
  .icon-arrow {
    width: 17px;
    height: 38px;
    margin-left: 7px;
  }
}

.green-border {
  display: inline-block;
  padding: 0 3px;
  border: 1px solid $green;
  border-radius: 5px;
}

.common-btn {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  padding: 12px;
  border-radius: 5px;
  &--orange {
    background-color: $orange;
    color: $white;
    &:hover {
      box-shadow: 0px 11px 34px rgba(255, 138, 0, 0.4);
    }
  }
}

@keyframes jump {
  from {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-7px);
  }

  to {
    transform: translateY(0);
  }
}

.form {
  display: flex;
  &--vertical {
    flex-direction: column;
  }
}

.form-group {
  margin-bottom: 10px;
  position: relative;
  &.error {
    .error-message {
      display: block;
    }
  }
  &:last-child {
    margin-bottom: 0;
  }
  input {
    border: none;
    padding: 17px 15px;
    background-color: $white;
    border-radius: 10px;
    font-size: 12px;
    line-height: 14px;
    color: $gray-text;
    width: 100%;
  }
  .error-message {
    display: none;
    font-size: 12px;
    color: #c50000;
    text-align: center;
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    pointer-events: none;
  }
}

.form-submit {
  background-color: $white;
  padding: 15px;
  border: 1px solid $green;
  border-radius: 10px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: $gray-text;
  width: 100%;
  cursor: pointer;
  transition: .3s ease-in-out;
  &:hover {
    background-color: $green;
    color: $white;
    transition: .3s ease-in-out;
  }
}