.main-slogan {
  background-color: $green;
  padding: 30px 0 20px;
  margin-bottom: 50px;
  @media (max-width: 767px) {
    padding: 20px 0;
  }
  &__subtitle {
    font-weight: 700;
    font-size: 30px;
    line-height: 36px;
    color: $white;
    @media (max-width: 767px) {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 6px;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 50px;
    line-height: 60px;
    color: $white;
    @media (max-width: 767px) {
      font-size: 24px;
      line-height: 30px;
    }
  }
}

.main-section {
  &__inner {
    border: 1px solid $lgray;
    box-sizing: border-box;
    border-radius: 20px;
    padding: 30px;
    position: relative;
    transition: .5s ease-in-out;
    @media (max-width: 767px) {
      padding: 25px 15px;
    }
    &:hover {
      box-shadow: 0 11px 50px rgba(36, 165, 68, 0.2);
      transition: .5s ease-in-out;
      .main-section__bg-img {
        transform: scale(1.1) translateX(-50px);
        transition: .5s ease-in-out;
      }
    }
  }
  &__content-box {
    position: relative;
    z-index: 1;
  }
  &__top-box {
    margin-bottom: 25px;
    @media (max-width: 767px) {
      margin-bottom: 15px;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 45px;
    line-height: 56px;
    color: $gray-text;
    @media (max-width: 767px) {
      font-size: 28px;
      line-height: 34px;
    }
    &--lg {
      font-weight: 700;
      font-size: 100px;
      line-height: 100px;
      @media (max-width: 767px) {
        font-size: 40px;
        line-height: 46px;
      }
    }
  }
  &__contacts-subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: $gray-text;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 10px;
    }
  }
  &__contacts-bottom-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 615px;
    margin-bottom: 95px;
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: flex-start;
      max-width: 100%;
      margin-bottom: 30px;
    }
  }
  &__contacts-data-box {
    display: flex;
    align-items: center;
    margin-right: 15px;
    @media (max-width: 767px) {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }
  &__contacts-social {
    margin-right: 20px;
    svg {
      width: 44px;
      height: 44px;
    }
  }
  &__contacts-phone-mail-box {
    display: flex;
    flex-direction: column;
  }
  &__contacts-phone {
    font-weight: 300;
    font-size: 18px;
    line-height: 21px;
    color: $gray-text;
    &:hover {
      text-decoration-color: $gray-text;
    }
  }
  &__contacts-mail {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: $gray-text;
    &:hover {
      text-decoration-color: $gray-text;
    }
    @media (max-width: 600px) {
      font-size: 14px;
      line-height: 19px;
    }
  }
  &__contacts-calc-link {
    display: flex;
    align-items: center;
    background-color: $orange;
    padding: 5px 15px;
    border-radius: 5px;
    max-width: 260px;
    flex-basis: 260px;
    &:hover {
      box-shadow: 0 11px 34px rgba(255, 138, 0, 0.4);
    }
    @media (max-width: 767px) {
      flex-basis: auto;
      max-width: none;
    }
    svg {
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      margin-right: 10px;
    }
    span {
      font-weight: 600;
      font-size: 20px;
      line-height: 24px;
      color: $white;
    }
  }
  &__advantages {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    @media (max-width: 1199px) {
      flex-wrap: wrap;
    }
  }
  &__advantages-item {
    flex-basis: 295px;
    max-width: 295px;
    border: 1px solid $lgray;
    border-radius: 10px;
    font-weight: 400;
    font-size: 19px;
    line-height: 24px;
    color: $gray-text;
    padding: 15px;
    position: relative;
    margin-right: 5px;
    &:last-child {
      margin-right: 0;
      @media (max-width: 1199px) {
        margin: 5px 3px;
      }
      @media (max-width: 600px) {
        margin: 5px 0;
      }
    }
    @media (max-width: 1199px) {
      flex-basis: 49%;
      max-width: 49%;
      margin: 5px 3px;
    }
    @media (max-width: 600px) {
      flex-basis: 100%;
      max-width: 100%;
      margin: 5px 0;
    }
    img {
      position: absolute;
      right: 10px;
      bottom: 10px;
    }
  }
  &__bg-img {
    position: absolute;
    top: 15px;
    right: -135px;
    transition: .5s ease-in-out;
    @media (max-width: 1199px) {
      display: none;
    }
  }
}

.transport-items {
  display: flex;
  margin: -10px -10px 30px -10px;
  @media (max-width: 991px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  &__item {
    display: flex;
    flex-direction: column;
    border: 1px solid $lgray;
    border-radius: 20px;
    padding-top: 20px;
    flex-basis: 310px;
    max-width: 310px;
    margin: 10px;
    overflow: hidden;
    transition: .3s ease-in-out;
    &:hover {
      border-color: $green;
      transition: .3s ease-in-out;
      .transport-items__img {
        transform: translateX(0);
        transition: .3s ease-in-out;
      }
      .transport-items__contacts {
        border-color: $green;
        transition: .3s ease-in-out;
      }
    }
  }
  &__img {
    align-self: flex-end;
    margin-bottom: auto;
    transform: translateX(30px);
    transition: .3s ease-in-out;
  }
  &__content {
    padding: 0 20px;
    margin-bottom: 15px;
    margin-top: 15px;
  }
  &__title {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: $gray-text;
    margin-bottom: 5px;
  }
  &__weight {
    font-size: 14px;
    line-height: 17px;
    color: $gray-text;
    margin-bottom: 10px;
  }
  &__price {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #FF8A00;
  }
  &__contacts {
    padding: 10px 23px;
    border-top: 1px solid $lgray;
    border-radius: 20px;
    display: flex;
    align-items: center;
    transition: .3s ease-in-out;
  }
  &__contacts-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #054428;
    margin-right: 15px;
  }
  &__contacts-item {
    margin-right: 10px;
    &:last-child {
      margin-right: 0;
    }
    svg {
      width: 28px;
      height: 28px;
    }
  }
}

.regular-transportation {
  margin: 90px 0 100px;
  padding: 60px 0 80px;
  background-color: #F0F0F0;
  @media (max-width: 600px) {
    margin: 40px 0;
    padding: 30px 0;
  }
  &__inner {
    position: relative;
  }
  &__content {
    position: relative;
    z-index: 1;
  }
  &__subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: $gray-text;
    margin-bottom: 50px;
    @media (max-width: 767px) {
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 20px;
    }
    @media (max-width: 600px) {
      br {
        display: none;
      }
    }
  }
  &__text {
    font-weight: 400;
    font-size: 36px;
    line-height: 42px;
    color: $gray-text;
    max-width: 640px;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      font-size: 20px;
      line-height: 24px;
    }
  }
  &__bottom-box {
    display: flex;
    align-items: center;
    @media (max-width: 600px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  .common-btn {
    margin-right: 25px;
    animation-duration: 1.5s;
    animation-name: jump;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    @media (max-width: 600px) {
      margin-bottom: 10px;
    }
  }
  &__bottom-box-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $green;
  }
  &__bg-img {
    position: absolute;
    right: -360px;
    top: -100px;
    @media (max-width: 1199px) {
      display: none;
    }
  }
}

.economy-items {
  margin: -10px -10px 100px -10px;
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 1365px) {
    justify-content: center;
  }
  @media (max-width: 767px) {
    margin: -10px -10px 50px -10px;
  }
  &__item {
    flex-basis: 420px;
    max-width: 420px;
    padding: 30px;
    border: 1px solid $lgray;
    border-radius: 20px;
    margin: 10px;
    position: relative;
    transition: .2s ease-in-out;
    &:hover {
      box-shadow: 0 11px 34px rgba(36, 165, 68, 0.2);
      transition: .2s ease-in-out;
    }
    @media (max-width: 1365px) {
      padding: 25px 50px 25px 15px;
      br {
        display: none;
      }
    }
    .icon {
      height: 70px;
      width: 70px;
      fill: transparent;
      position: absolute;
      right: 15px;
      bottom: 15px;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    color: $gray-text;
    margin-bottom: 10px;
  }
  &__text {
    font-size: 18px;
    line-height: 27px;
    color: $gray-text;
  }
}

.transport-types {
  margin-bottom: 100px;
  @media (max-width: 767px) {
    margin-bottom: 50px;
  }
  &__subtitle {
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    color: $gray-text;
    margin-bottom: 30px;
    @media (max-width: 767px) {
      font-size: 18px;
      line-height: 24px;
    }
  }
}

.transport-types-items {
  display: flex;
  margin: -20px -20px 100px -20px;
  @media (max-width: 1199px) {
    margin: -10px -10px 100px -10px;
  }
  @media (max-width: 991px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  @media (max-width: 767px) {
    margin: -10px -10px 50px -10px;
  }
  &__item {
    font-size: 21px;
    line-height: 26px;
    color: $gray-text;
    padding: 20px 15px;
    box-shadow: 0 11px 34px rgba(36, 165, 68, 0.2);
    border-radius: 10px;
    margin: 20px;
    @media (max-width: 1199px) {
      margin: 10px;
    }
    @media (max-width: 767px) {
      font-size: 15px;
      line-height: 20px;
      flex-basis: calc(50% - 20px);
      max-width: calc(50% - 20px);
    }
  }
}

.goods-types {
  display: flex;
  margin: -15px -15px 30px -15px;
  @media (max-width: 991px) {
    flex-wrap: wrap;
    justify-content: center;
  }
  &__item {
    flex-basis: 300px;
    max-width: 300px;
    margin: 15px;
    display: flex;
    flex-direction: column;
    border: 1px solid $lgray;
    box-sizing: border-box;
    border-radius: 20px;
    overflow: hidden;
  }
  &__img {
    img {
      object-fit: contain;
    }
  }
  &__content {
    padding: 15px;
    border-top: 1px solid $lgray;
  }
  &__name {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: $green;
    margin-bottom: 2px;
  }
  &__text {
    font-size: 14px;
    line-height: 17px;
    color: $gray-text;
  }
}

.poll {
  background-color: $green;
  padding: 60px 0 70px;
  margin-bottom: 100px;
  @media (max-width: 767px) {
    padding: 25px 0;
    margin-bottom: 50px;
  }
  &__inner {
    display: flex;
    padding: 0 50px;
    @media (max-width: 991px) {
      flex-direction: column;
    }
    @media (max-width: 767px) {
      padding: 0;
      align-items: center;
    }
  }
  .icon-poll {
    width: 102px;
    height: 133px;
    margin-right: 70px;
    flex-shrink: 0;
    @media (max-width: 991px) {
      margin-right: 0;
      margin-bottom: 30px;
    }
    @media (max-width: 767px) {
      width: 60px;
      height: 90px;
      margin-bottom: 15px;
    }
  }
  &__text {
    font-size: 24px;
    line-height: 28px;
    color: $white;
    margin-bottom: 8px;
    @media (max-width: 767px) {
      font-size: 18px;
      line-height: 24px;
    }
  }
  &__title {
    color: $white;
    margin-bottom: 0;
    @media (max-width: 1199px) {
      br {
        display: none;
      }
    }
  }
}

.personal-manager {
  &__items {
    display: flex;
    margin: -10px;
    @media (max-width: 1199px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @media (max-width: 600px) {
      margin: -10px 0;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-basis: 420px;
    max-width: 420px;
    padding: 20px 25px 50px 25px;
    margin: 10px;
    border: 1px solid $lgray;
    border-radius: 10px;
    @media (max-width: 600px) {
      padding: 15px;
      margin: 10px 0;
    }
    .icon {
      width: 120px;
      height: 100px;
      margin-bottom: 25px;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 25px;
    line-height: 30px;
    color: $gray-text;
    margin-bottom: 10px;
    @media (max-width: 600px) {
      font-size: 20px;
      line-height: 26px;
    }
  }
  &__text {
    p {
      font-size: 20px;
      line-height: 24px;
      color: $gray-text;
      margin-bottom: 15px;
      @media (max-width: 600px) {
        font-size: 18px;
        line-height: 22px;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    ul {
      list-style: initial;
      margin-bottom: 15px;
      padding-left: 25px;
    }
  }
}

.we-proud-of {
  margin-bottom: 70px;
  &__inner {
    display: flex;
    align-items: center;
    @media (max-width: 991px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__content {
    flex-basis: 740px;
    max-width: 740px;
    margin-right: 25px;
  }
  &__title {
    margin-bottom: 15px;
  }
  &__text {
    font-size: 20px;
    line-height: 24px;
    color: $gray-text;
    @media (max-width: 767px) {
      font-size: 16px;
      line-height: 22px;
      br {
        display: none;
      }
    }
  }
  &__img {
    flex-shrink: 0;
    @media (max-width: 991px) {
      flex-shrink: 1;
    }
    img {
      object-fit: contain;
      width: 100%;
      height: auto;
    }
  }
}

.new-level {
  background-color: #F0F0F0;
  padding: 60px 0;
  position: relative;
  margin-bottom: 140px;
  @media (max-width: 767px) {
    margin-bottom: 40px;
  }
  &__content {
    max-width: 890px;
    position: relative;
    z-index: 1;
  }
  &__text {
    font-size: 35px;
    line-height: 41px;
    color: $gray-text;
    margin-bottom: 12px;
    @media (max-width: 767px) {
      font-size: 22px;
      line-height: 28px;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 48px;
    color: $green;
    @media (max-width: 767px) {
      font-size: 26px;
      line-height: 32px;
    }
  }
  &__bg-img {
    position: absolute;
    right: calc(50% - 860px);
    top: -88px;
    @media (max-width: 1199px) {
      display: none;
    }
  }
}

.testimonials {
  margin-bottom: 100px;
  @media (max-width: 767px) {
    margin-bottom: 60px;
  }
  &__items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: -10px -10px 50px -10px;
    @media (max-width: 767px) {
      margin: -10px -10px 30px -10px;
    }
  }
  &__item {
    flex-basis: 420px;
    max-width: 420px;
    margin: 10px;
    position: relative;
    transition: .2s ease-in-out;
    &:hover {
      filter: drop-shadow(0px 11px 34px rgba(36, 165, 68, 0.2));
      transition: .2s ease-in-out;
      .testimonials__item-icon {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
        transition: .2s ease-in-out;
      }
    }
    img {
      @media (max-width: 991px) {
        width: 100%;
        height: auto;
        object-fit: contain;
      }
    }
  }
  &__item-icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(0.85);
    opacity: 0;
    transition: .2s ease-in-out;
    svg {
      width: 100px;
      height: 100px;
    }
  }
}

.directions {
  margin-bottom: 90px;
  @media (max-width: 767px) {
    margin-bottom: 40px;
  }
  &__title {
    margin-bottom: 60px;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: -12px -12px 45px -12px;
    @media (max-width: 767px) {
      margin: -12px -12px 30px -12px;
    }
  }
  &__item {
    flex-basis: 306px;
    max-width: 306px;
    margin: 12px;
    border: 1px solid $lgray;
    border-radius: 20px;
    overflow: hidden;
  }
  &__item-content {
    padding: 20px;
    border-top: 1px solid $lgray;
  }
  &__item-title {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: $green;
    margin-bottom: 2px;
  }
  &__item-subtitle {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: $gray-text;
    margin-bottom: 10px;
  }
  &__item-text {
    font-size: 14px;
    line-height: 17px;
    color: $gray-text;
  }
}

.callback {
  margin-bottom: 100px;
  @media (max-width: 767px) {
    margin-bottom: 50px;
  }
  &__inner {
    background: #f0f0f0;
    border-radius: 20px;
    padding: 50px 35px;
    display: flex;
    justify-content: space-between;
    @media (max-width: 1199px) {
      flex-direction: column;
      max-width: 650px;
      margin: 0 auto;
    }
    @media (max-width: 600px) {
      padding: 35px 10px;
    }
  }
  &__box {
    flex-basis: calc(100% / 3 - 30px);
    margin-right: 25px;
    @media (max-width: 1199px) {
      max-width: 550px;
      margin-right: 0;
      margin-bottom: 30px;
    }
    &:last-child {
      margin-right: 0;
      @media (max-width: 1199px) {
        margin-bottom: 0;
      }
    }
  }
  &__phone {
    font-weight: 700;
    font-size: 50px;
    line-height: 60px;
    color: #24A544;
    margin-bottom: 10px;
    white-space: nowrap;
    @media (max-width: 600px) {
      font-size: 34px;
      line-height: 40px;
    }
  }
  &__phone-tip {
    font-size: 33px;
    line-height: 38px;
    color: $gray-text;
    @media (max-width: 600px) {
      font-size: 26px;
      line-height: 30px;
    }
  }
  &__text {
    font-size: 24px;
    line-height: 29px;
    color: $gray-text;
    @media (max-width: 600px) {
      font-size: 20px;
      line-height: 24px;
    }
  }
}

.contacts {
  margin-bottom: 100px;
  @media (max-width: 767px) {
    margin-bottom: 50px;
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 65px;
  }
  &__top-box {
    flex-basis: calc(50% - 20px);
    max-width: calc(50% - 20px);
    margin-right: 20px;
    @media (max-width: 991px) {
      flex-basis: 100%;
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 30px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__title {
    @media(max-width: 991px) {
      margin-bottom: 0;
    }
  }
  &__social {
    display: flex;
  }
  &__social-item {
    width: 80px;
    height: 80px;
    margin-right: 30px;
    &:last-child {
      margin-right: 0;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__text {
    font-size: 20px;
    line-height: 24px;
    color: $gray-text;
    p {
      margin-bottom: 20px;
    }
    ul {
      list-style: initial;
      padding-left: 25px;
    }
    li {
      margin-bottom: 10px;
    }
    a {
      color: $gray-text;
      &:hover {
        text-decoration-color: $gray-text;
      }
    }
    .contacts__text-reqs {
      color: $green;
      text-decoration-color: $green;
      &:hover {
        text-decoration-color: transparent;
      }
    }
  }
  &__bottom {
    display: flex;
    @media (max-width: 991px) {
      flex-wrap: wrap;
    }
  }
  &__bottom-box {
    margin-right: 20px;
    @media (max-width: 991px) {
      flex-basis: 100%;
      margin-right: 0;
      margin-bottom: 30px;
    }
    img {
      @media (max-width: 991px) {
        width: 100%;
        height: auto;
      }
    }
    &:last-child {
      margin-right: 0;
    }
  }

}