.mobile-menu-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow-x: hidden;
  pointer-events: none;
  z-index: 3;
}

.mobile-menu {
  width: 300px;
  height: 100%;
  background-color: $green;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateX(100%);
  transition: .3s ease-in-out;
  z-index: 3;
  display: none;
  pointer-events: initial;
  @media (max-width: 991px) {
    display: block;
  }
  &.opened {
    transform: translateX(0);
    transition: .3s ease-in-out;
  }
  .close-cross {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 10px;
    top: 10px;
    fill: $white;
    cursor: pointer;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }
  &__inner {
    max-height: 100vh;
    overflow-y: auto;
    padding: 45px 20px 30px;
    position: relative;
  }
}